<template>
  <modal modalWidth="min(26em,90%)" :buttonColor="colorTheme">
    <template #content>
      <br>
      <h3 style="text-align:center">Маєте питання?</h3>
      <div class='modal-text'>Напишіть нам у месенджері:</div>
      <div class='display: flex'>
        <a class="icon-social" href='https://telegram.me/sky_wing' target="_blank" rel="noopener noreferrer" ><img src='@/assets/icon-telegram.png'></a>
        <a class="icon-social" href='viber://chat?number=%2B380665299228' target="_blank" rel="noopener noreferrer" ><img src='@/assets/icon-viber.png'></a>
      </div>
      <div class="modal-separator">або</div>
      <div class="modal-text">Скористайтесь цією формою і отримайте відповідь на електронну пошту:</div>
      <input-textarea v-model='reportData.text' placeholder="Питання чи опис проблеми" ref='inputText'/>
    </template>
    <template #buttons>
      <button class="button-small" :style="{ background: colorTheme }" @click="report()">Надіслати</button>
    </template>
  </modal>
</template>

<script>
import controller from '@/api/controller'
import inputText from '@/components/inputs/input-text.vue'
import inputEmail from '@/components/inputs/input-email.vue'
import inputTextarea from '@/components/inputs/input-textarea.vue'
import modal from '@/components/modals/modal-basic.vue'
export default {
  components: { inputText, inputEmail, inputTextarea, modal },
  data() { return {
    colorTheme: localStorage.getItem('colorTheme'),
    reportData: {
      text: ''
    },
  }},
  computed: {
    inputs() { return Object.keys(this.$refs).map(e => this.$refs[e]) },
  },
  methods: {
    async report() {
      this.inputs.map(e => e.v$.$touch())
      if (!this.inputs.some(e => e.v$.$error)) {
        await controller.report('Нове звернення на skywing.online', this.reportData)
        this.$emit("update:visible", false)
      }
    }
  }
}
</script>

<style lang="scss" scoped>
//* {border: 1px solid red}
.icon-social {
  width: 3.5em;
  height: 3.5em;
  margin: 10px;
  &:hover { opacity: .75 }
  &:active { opacity: .75 }
  img {
    width: 100%;
    height: 100%;
  }
}
.modal-separator {
  display: flex;
  gap: 10px;
  margin: 1.5em 0;
  font-size: .9em;
  color: #0004;
  &::before, &::after{
    content: "";
    flex: 1 1;
    border-bottom: 1px solid;
    margin: auto;
  }
}
</style>