import instance from '@/api/instance'

export default {

  async reg(username, email, password) { return instance.post(`reg`, { username, email, password }) },
  async login(username, password) { return instance.post(`login`, { username, password }) },
  async logout() { instance.post(`logout`) },
  async resendActivationLink(email) { instance.post(`resend-activation-link`, { email }) },
  async requestPasswordRenewal(email) { instance.post(`request-password-renewal`, { email }) },
  async renewPassword(password, activationLink) { instance.post(`renew-password`, { password, activationLink }) },
  async report(topic, obj) { instance.post(`report`, { topic, obj }) },

  async getModules() { return instance.get(`modules`) },
  async getLessons(moduleName) { return instance.get(`lessons/${moduleName}`) },
  async getTasks(lessonName, lessonId) { return instance.get(`tasks/?name=${lessonName}&id=${lessonId}`) },
  async skipAnswer(taskId) { return instance.post(`skip`, { taskId }) },
  async postAnswer(moduleId, blockId, lessonId, taskData) { return instance.post(`answer`, { moduleId, blockId, lessonId, taskData }) },

  async getPrefabs() { return instance.get(`prefabs`) },
  async getPrefab(prefabName) { return instance.get(`prefab/${prefabName}`) },
  async createPrefab(prefabName) { return instance.get(`prefab/create/${prefabName}`) },
  async deletePrefab(prefabName) { return instance.get(`prefab/delete/${prefabName}`) },
  async postPrefabAnswer(prefabName, taskData) { return instance.post(`prefab/answer`, { prefabName, taskData }) },

  async getStats() { return instance.get(`stats`) },
  async getRating(limit) { return instance.get(`rating/${limit}`) },

}