export default { 
  
  methods: {

    mix3(value) {
      const StartColor  = 0xff4d4d; // Green
      const MiddleColor = 0xe1b74d; // Orange
      const EndColor    = 0x4da64d; // Red

      const getComonents = (color) => Array.from({length: 3}, (_, i) => Math.floor(color / 16 ** (2 * i) % 16 ** 2))
      const interpolate = (arr1, arr2, percent) => arr1.map((v, index) => Math.floor(v + (arr2[index] - v) * (percent / 100)))
      
      let percentage = Math.max( 0, Math.min(value, 100) )
      let [start, end, v] = percentage < 50 
          ? [StartColor, MiddleColor, percentage * 2 ] 
          : [MiddleColor, EndColor, (percentage - 50) * 2]

      let interpoled = interpolate(getComonents(start), getComonents(end), v)
      let hex = interpoled.reduce((n, component, index) => n + component * (16 ** (index * 2)), 0)
      return ("#" + hex.toString(16).padStart(6, '0'))
    },
  
    lighten(hex, percent) {
      // strip the leading # if it's there
      hex = hex.replace(/^\s*#|\s*$/g, '');

      if(hex.length == 3){
          hex = hex.replace(/(.)/g, '$1$1');
      }

      var r = parseInt(hex.substr(0, 2), 16),
          g = parseInt(hex.substr(2, 2), 16),
          b = parseInt(hex.substr(4, 2), 16);

      return '#' +
        ((0|(1<<8) + r + (256 - r) * percent / 100).toString(16)).substr(1) +
        ((0|(1<<8) + g + (256 - g) * percent / 100).toString(16)).substr(1) +
        ((0|(1<<8) + b + (256 - b) * percent / 100).toString(16)).substr(1);
    }

  }

}