<template>

  <navbar>
    <i v-if="!avatarURL" class="material-icons-outlined">account_circle</i>
    <img v-show='avatarURL' :src="avatarURL" alt class="my-avatar">
    <sidebar v-if="username">
      &ensp;<span class="link">{{ username }}</span>
    </sidebar>
    <router-link v-else class="link" to="auth">Увійти</router-link>
  </navbar>

  <div class="header-wrapper" :style='{
    background: `linear-gradient(#0006, #0006), url(${backgroundImage})`,
    backgroundSize: "cover", backgroundPosition: `left 0 top ${backgroundOffset}%`}'>
    <slot name="header"></slot>
  </div>

  <div class="body-wrapper">
    <slot name='body'></slot>
  </div>

</template>

<script>
import navbar from '../components/navbar.vue'
import sidebar from '../components/sidebar.vue'
export default {
  name: 'main-layout',
  components: { navbar, sidebar },
  props: {
    backgroundImage: String,
    backgroundOffset: String,
  },
  data() { return {
    username: localStorage.getItem('username'),
    avatarURL: localStorage.getItem('avatar'),
  }}
}
</script>

<style lang="scss" scoped>
@import '../styles.scss';
.header-wrapper {
  //width: min(100%, 70em);
  margin: auto;
  position: relative;
}
.header-wrapper {
  padding: 1.5em min(4vw, 40px) 2.5em min(4vw, 40px);
  box-sizing: border-box;
  @media (max-width: 480px) { padding: 30px }
}
.body-wrapper {
  background: #eee;
  padding: 2em min(4vw, 40px) 7vmin min(4vw, 40px);
  box-sizing: border-box;
  @media (max-width: 480px) { padding: 20px 10px }
}
i { font-size: 1.75em; }
.my-avatar {
  width: 1.75em;
  height: 1.75em;
