<template>
  <modal-feedback v-model:visible="modalFeedbackVisible"/>

  <label for="toggle-menu">
    <slot/>
  </label>
  <input type="checkbox" id="toggle-menu" ref='checkbox'>
  <div class="underlay" @click='$refs.checkbox.click()'></div>
  <div class="side-menu">
    <div class='menu-entry' v-for='menuEntry in menuEntries' @click='menuEntry.action(); $refs.checkbox.click()'>
      <div class='material-icons-outlined menu-entry-icon'>{{ menuEntry.icon }}</div>
      <div class="menu-entry-label">{{ menuEntry.label }}</div>
    </div>
  </div>
</template>

<script>
import modalFeedback from '../components/modals/modal-feedback.vue'
export default {
  components: { modalFeedback },
  data() { return {
    modalFeedbackVisible: false,
    menuEntries: [
      {
        icon: 'bookmark_border',
        label: 'Мої модулі',
        action: this.gotoModules
      },
      {
        icon: 'bar_chart',
        label: 'Статистика',
        action: this.gotoStats
      },
      {
        icon: 'grade',
        label: 'Рейтинг',
        action: this.gotoRating
      },
      {
        icon: 'edit',
        label: 'Написати експерту',
        action: this.showFeedbackModal
      },
      {
        icon: 'logout',
        label: 'Вийти з акаунта',
        action: this.logout
      },
    ],
  }},
  methods: {
    showFeedbackModal() { this.modalFeedbackVisible = true },
    gotoModules() { this.$router.push('modules') },
    gotoStats() { this.$router.push('stats') },
    gotoRating() { this.$router.push('rating') },
    logout() {
      localStorage.removeItem('access')
      localStorage.removeItem('refresh')
      localStorage.removeItem('username')
      localStorage.removeItem('avatar')
      this.$router.push('/')
    },
  }
}
</script>

<style lang="scss">
.underlay {
  display: none;
  position: fixed;
  left: 0; top: 0;
  width: 100%;
  height: 100%;
  z-index: 5;
}

.side-menu {
  position: fixed;
  display: flex;
  z-index: 6;
  width: 11em;
  height: 100%;
  right: -400px; top: 0;
  transition: all .5s;
  box-sizing: border-box;
  flex-direction: column;
  background: #333d;
  overflow: scroll;
  scrollbar-width: none;
  &::-webkit-scrollbar { display: none }
  .menu-entry {
    text-align: center;
    padding: 2em 0;
    border-bottom: 1px solid #fff1;
    opacity: .75;
    &:hover { 
      opacity: 1;
      background: #fff2;
    }
  }
  .menu-entry-icon {
    margin-bottom: .25em;
    font-size: 3.5em;
    font-weight: 100;
    color: #fff;
  }
  .menu-entry-label {
    font-size: .9em;
    color: #fff;
  }
}

#toggle-menu {
  appearance: none;
  &:checked ~ .underlay { display: block }
  &:checked ~ .side-menu { right: 0 }
}
</style>