<template>
  <input type="text" 
    class="input-textfield-line" 
    :class='{ "input-textfield-invalid": v$.modelValue.$error }'
    :value="modelValue"
    @input="$emit('update:modelValue', $event.target.value.trim())"
    :placeholder='placeholder'
  >
  <div v-if='v$.modelValue.$error'>
    <div class='input-error-message' v-if='v$.modelValue.required.$invalid'>Поле має бути заповнене</div>
    <div class='input-error-message' v-if='v$.modelValue.minLength.$invalid'>Поле має містите більше {{ min }} символів</div>
    <div class='input-error-message' v-if='v$.modelValue.alphaNum.$invalid'>Поле може містити лише латинські літери і цифри</div>
  </div>
</template>

<script>
import useVuelidate from '@vuelidate/core'
import { required, minLength, alphaNum } from '@vuelidate/validators'
export default {
  props: {
    modelValue: { type: String, required: false, default: '' },
    placeholder: { type: String, required: false, default: '' },
    min: { type: [String, Number], required: false, default: 3 },
    isRequired: { type: Boolean, required: false, default: true },
    isAlphanumeric: { type: Boolean, required: false, default: false },
  },
  data() { return {
    v$: useVuelidate(),
  }},
  validations() { return {
    modelValue: { 
      required,
      minLength: minLength(3),
      alphaNum: this.isAlphanumeric ? alphaNum : true
    },
  }},
}
</script>